.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

nav {
  background-color: rgb(0, 10, 28); 
}

#sidenav-overlay { z-index: 1; }

#headshot {
  display: block;
  margin: auto;
}

.App-header {
  color: white;
  height: 100vh;
  margin-top: -64px;
  padding-top: 64px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background: url(./img/galaxy.jpg) no-repeat center center;
  background-size: cover;
}

.App-title {
  font-size: 1.5em;
  text-align: center;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.connect-icon {
  height: 4rem;
  width: auto;

}

.connect-methods p {
  margin-top: 0;
  padding-bottom: 0;
}

.modal .modal-content {
    padding-bottom: 0px;
}

.modal .modal-content a {color:  rgb(0, 10, 28);}


